<template>
  <v-app id="app">

    <v-navigation-drawer
      class="text-left"
      v-model="primaryDrawer.model"
      :clipped="primaryDrawer.clipped"
      :floating="primaryDrawer.floating"
      :mini-variant="primaryDrawer.mini"
      :permanent="primaryDrawer.type === 'permanent'"
      :temporary="primaryDrawer.type === 'temporary'"
      app
      overflow
    >
     <v-list dense >
      <v-list-item-group v-model="item" color="primary" >
        <v-list
          class="v-list__tile"
          v-for="(item, i) in items"
          :key="i"
        >
           <v-subheader v-if="item.heading"  v-text="item.heading"/>
          <v-list-group
            v-else-if="item.children"
            :key="item.text"
            v-model="item.model"
            :prepend-icon="item.model ? item.icon : item['icon-alt']"
            append-icon=""
          >
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title v-text="item.text" />
              </v-list-item-content>
            </template>
            <v-list-item
              v-for="(child, j) in item.children"
              :key="j+i"
            >
              <v-list-item-action>
                <v-icon v-text="child.icon"/>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title v-text="child.text"/>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>

          <v-list-item v-else :key="i" :to="item.url" link>
            <v-list-item-icon>
              <v-icon v-text="item.icon"/>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="item.text">
              </v-list-item-title>
            </v-list-item-content>
            </v-list-item>
        </v-list>
      </v-list-item-group>
    </v-list>
    </v-navigation-drawer>

    <v-app-bar
      :clipped-left="primaryDrawer.clipped"
      app
    >
      <v-app-bar-nav-icon
        v-if="primaryDrawer.type !== 'permanent'"
        @click.stop="primaryDrawer.model = !primaryDrawer.model"
      />
      <v-toolbar-title>MedDwak</v-toolbar-title>

      <v-spacer/>

          <!-- menu-->
      <v-btn v-if="isLoggedIn"  @click="logout" text small> Logout </v-btn>
      <template v-else>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn  v-bind="attrs"
              v-on="on" text small> Login </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">this</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                     <v-form
                    class="mr-4 mt-4"
                    @submit.prevent="login"
                    ref="form"
                    v-model="valid"
                    lazy-validation
                  >
                  <v-text-field
                  filled
                  dense
                  prepend-icon="mdi-account"
                  v-model="username"
                  :rules="usernameRules"
                  label="Username"
                  required/>

                  <v-text-field
                    prepend-icon="mdi-lock"
                    v-model="password"
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :rules="[rules.required, rules.min]"
                    :type="show1 ? 'text' : 'password'"
                    name="input-10-1"
                    label="Password"
                    hint="At least 8 characters"
                    filled
                    dense
                    counter
                    @click:append="show1 = !show1"
                  />
                </v-form>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer/>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="validate">Login</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-btn text to="/register" small> Register </v-btn>
      </template>

          <v-menu bottom
                  left
                  offset-y
                  :close-on-content-click="false"
                  :nudge-width="200"
                  offset-x
                  transition="slide-y-transition">

            <template v-slot:activator="{ on, attrs }">
              <v-btn
                icon
                v-bind="attrs"
                v-on="on"
              >
                <v-icon>mdi-dots-vertical</v-icon>
              </v-btn>
            </template>

            <v-card>
              <v-list>
                <v-list-item>
                  <v-list-item-avatar>
                    <img src="https://cdn.vuetifyjs.com/images/john.jpg" alt="John">
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title>John Leider</v-list-item-title>
                    <v-list-item-subtitle>Founder of Vuetify.js</v-list-item-subtitle>
                  </v-list-item-content>

                  <v-list-item-action>
                    <v-btn
                      :class="fav ? 'red--text' : ''"
                      icon
                      @click="fav = !fav"
                    >
                      <v-icon>mdi-heart</v-icon>
                    </v-btn>
                  </v-list-item-action>
                </v-list-item>
              </v-list>

              <v-divider/>

              <v-list>

                <v-radio-group
                  v-model="primaryDrawer.type">
                  <v-layout v-for="drawer in drawers" :key="drawer">
                    <v-list-item>
                      <v-list-item-action>
                        <v-radio
                        :key="drawer"
                        :value="drawer.toLowerCase()"
                        primary
                        />
                      </v-list-item-action>
                      <v-list-item-title v-text="drawer"/>
                    </v-list-item>
                  </v-layout>
                </v-radio-group>

                <v-list-item>
                  <v-list-item-action>
                     <v-switch v-model="$vuetify.theme.dark"  color="purple" />
                  </v-list-item-action>
                  <v-list-item-title>Dark mode</v-list-item-title>
                </v-list-item>

                <v-list-item>
                  <v-list-item-action>
                     <v-switch v-model="primaryDrawer.clipped" color="purple" />
                  </v-list-item-action>
                  <v-list-item-title>Clipped</v-list-item-title>
                </v-list-item>

                <v-list-item>
                  <v-list-item-action>
                    <v-switch v-model="primaryDrawer.floating" color="purple"/>
                  </v-list-item-action>
                <v-list-item-title>Floating</v-list-item-title>
                </v-list-item>

                <v-list-item>
                  <v-list-item-action>
                    <v-switch v-model="primaryDrawer.mini" color="purple" />
                  </v-list-item-action>
                <v-list-item-title>Mini</v-list-item-title>
                </v-list-item>
                <v-list-item>
                  <v-list-item-action>
                    <v-switch v-model="footer.inset" color="purple" />
                  </v-list-item-action>
                <v-list-item-title>inset</v-list-item-title>
                </v-list-item>
              </v-list>

              <v-card-actions>
                <v-spacer/>
                <v-btn text @click="menu = false">Cancel</v-btn>
                <v-btn color="primary" text @click="menu = false">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-menu>
    </v-app-bar>

    <v-main>
      <v-container fluid>
        <router-view :key="$route.fullPath"/>
      </v-container>
    </v-main>

    <v-footer
      :inset="footer.inset"
      app
    >
      <small><span class="px-4">All rights received &copy; {{ new Date().getFullYear() }}</span></small>
    </v-footer>
  </v-app>
</template>

<script>

export default {
  computed: {
    isLoggedIn: function () {
      return '' // this.$store.getters.isLoggedIn
    }
  },

  methods: {
    close () {
      this.dialog = false
    },

    validate () {
      if (this.$refs.form.validate()) {
        this.login()
      }
    },
    login: function () {
      const username = this.username
      const password = this.password
      this.$store
        .dispatch('login', { username, password })
        .then(() => this.$router.go(0))
        .catch(err => console.log(err))
    },

    logout: function () {
      this.$store.dispatch('logout').then(() => {
        this.$router.go(0)
      })
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    }
  },
  data: () => ({
    valid: true,
    username: '',
    usernameRules: [
      v => !!v || 'Username is required',
      v => (v && v.length <= 10) || 'Name must be less than 10 characters'
    ],
    password: '',
    show1: false,
    rules: {
      required: value => !!value || 'Required.',
      min: v => v.length >= 8 || 'Min 8 characters'
    },
    drawers: ['Default (no property)', 'Permanent', 'Temporary'],
    dialog: false,
    isDarkMode: !!localStorage.getItem('isDark'),
    primaryDrawer: {
      model: null,
      type: 'Permanent',
      clipped: true,
      floating: true,
      mini: false
    },
    footer: {
      inset: false
    },
    items: [
      { heading: 'Menu' },
      { icon: 'mdi-contacts', text: 'Home', url: '/' },
      { icon: 'mdi-cellphone-link', text: 'Medicaments', url: '/medicaments' },
      { icon: 'mdi-history', text: 'About', url: '/about' }
    ]
  })
}

</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.v-list__tile{
  padding: 0;
}
</style>
